<template>
    <div :class="{'content-none': isRecommend == 1}">
        <div class="btn-audit clearfix" v-if="isRecommend == 0 && (distinguishCharacter =='mkt' || distinguishCharacter =='cstm')">
            <Button class="btn-recommend" @click="refuseRecommend">拒绝推荐</Button>
            <Button type="primary" class="btn-recommend" @click="agreeRecommend">同意推荐</Button>
        </div>
        <AdtMyModal title="不同意推荐方案原因" okText="确定" cancelText="取消货运要求" showThirdTitle="编辑货运要求" :isShowThirdBtn="true" @on-visible-change="isShow" :showModal="isModalRecommend" @on-ok="save()" @on-cancel="cancel()" @handleFeatures="handleFeatures">
            <Form ref="formInline" :model="formInline" :rules="ruleInline" inline>
                <FormItem prop="remark" label="不同意推荐原因:">
                    <Input type="textarea" v-model="formInline.remark" style="width:400px"></Input>
                </FormItem>
            </Form>
        </AdtMyModal>
        <adt-logistics-route :logisticsRouteList="listDatas" @clickPath="clickPath" />
        <!-- 调度需要展示的提示 -->
        <Alert v-if="distinguishCharacter == 'dspt'" class="alart-box" type="warning" show-icon>根据货运要求匹配的推荐物流服务方案请在详情内确认方案后推荐给销售！</Alert>
        <div class="btn-pagina">
            <span v-if="isRecommend == 0 || isRecommend == 1 || isRecommend == 3" class="title">推荐物流服务方案</span>
            <div v-if="isRecommend == 2" class="btn-tabs" >
                <adt-btns :btnIndex="btnIndex" :titleNames="titleNames" @handleBtn="handleBtn" />
                <AdtButton v-if="btnIndex == 1 && distinguishCharacter == 'dspt' || btnIndex == 2 && distinguishCharacter == 'mkt'">
                    <Button @click="recommendCheck()">推荐勾选<Icon type="ios-add-circle-outline" /></Button>
                    <Button @click="recommendAll()">推荐全部<Icon type="ios-add-circle-outline" /></Button>
                    <Button @click="recommendCurrent()">推荐当前路线方案<Icon type="ios-add-circle-outline" /></Button>
                </AdtButton>
            </div>
            <adt-paginator class="pagination" :currentPage="~~page" :total="~~total" />
        </div>
        <div class="public-component-line"></div>
        <div class="public-list">
            <div class="public-component-card" v-for="(logistics, id) in listData" :key="id">
                <AdtListItem :logistics="logistics" :showCheckbox="isCheckbox" @checkData="checkData">
                    <template slot="tab-title">
                        <span>物流方案编号:{{ logistics.logisticsSchemeCode }}</span>
                    </template>
                    <template slot="tab-operat">
                        <span v-if="distinguishCharacter == 'dspt'" :class="{'card-status-success': logistics.statusCode == 'disagree', 'card-status-error': logistics.statusCode == 'refuse', 'card-status': logistics.statusCode == 'agree' }">{{ logistics.status }}</span>
                        <Poptip placement="right-start" class="card-icon">
                            <span>编辑</span>
                            <Icon type="md-arrow-dropdown" />
                            <!-- 客户的操作 -->
                            <div v-if="distinguishCharacter == 'cstm'" class="card-poptip-font" slot="content">
                                <p @click="detail(logistics)">查看详情</p>
                                <p @click="delegate(logistics)">发起委托</p>
                                <!-- <p @click="collect(logistics)" class="failure">收藏</p> -->
                                <!-- <p @click="uncollect(logistics)" class="failure">取消收藏</p> -->
                            </div>
                            <!-- 销售的操作 -->
                            <div v-if="distinguishCharacter == 'mkt'" class="card-poptip-font" slot="content">
                                <p @click="detail(logistics)">查看详情</p>
                                <p @click="delegate(logistics)">发起委托</p>
                                <p @click="recommend(logistics)">推荐客户</p>
                                <!-- <p @click="collect(logistics)" class="failure">收藏</p> -->
                            </div>
                            <!-- 调度的操作 -->
                            <div v-if="distinguishCharacter == 'dspt'" class="card-poptip-font" slot="content">
                                <p @click="delegate(logistics)">发起委托</p>
                                <p @click="edit(logistics)">编辑方案</p>
                                <p @click="confirm(logistics)">确认方案</p>
                                <p @click="detail(logistics)">查看详情</p>
                                <p @click="recommend(logistics)">推荐销售</p>
                            </div>
                        </Poptip>
                    </template>
                    <template slot="tab-content">
                        <div class="card-route">
                            <div class="card-content" v-for="(location, index) in logistics.routeLists" :key="index">
                                <span class="card-outset">{{ location.departurePort }}</span>
                                <div class="card-transit">
                                    <div class="card-way">
                                        <svg class="icon" aria-hidden="true">
                                            <use v-if="location.trspMode == '陆运'" fill="#FFBB32" xlink:href="#icon-andaotongcaidanICON_huoche"></use>
                                            <use v-if="location.trspMode == '水运'" fill="#FFBB32" xlink:href="#icon-andaotongcaidanICON_lunchuan"></use>
                                            <use v-if="location.trspMode == '3'" fill="#FFBB32"
                                            xlink:href="#icon-feiji800"></use>
                                        </svg>
                                    </div>
                                    <div class="card-line">
                                        <svg class="icon" aria-hidden="true">
                                            <use fill="#02ADB5" xlink:href="#icon-andaotongcaidanICON_jiantou3"></use>
                                        </svg>
                                    </div>
                                    <div class="card-genre">{{ location.logisticsType }}{{ location.trspType }}</div>
                                </div>
                                <template v-if="index === logistics.routeLists.length - 1">
                                    <span class="card-address">{{ location.destinationPort }}</span>
                                </template>
                            </div>
                        </div>
                    </template>
                    <template slot="tab-bottom">
                        <!-- 客户看到的最高时效/价格 -->
                        <div class="card-bottom" v-if="distinguishCharacter == 'cstm'">
                            <span>时效: {{ logistics.hghtTmeff }}{{ logistics.tmeffUnit}}</span>
                            <span></span>
                            <span class="card-right">
                                <span>价格:</span>
                                <span class="card-right-size">{{ logistics.lwstPrice }}</span>
                                <span>/{{logistics.offerCurr}}</span>
                            </span>
                        </div>
                        <!-- 调度/销售看到的时效段/价格区间 -->
                        <div class="card-bottom"  v-if="distinguishCharacter == 'mkt' || distinguishCharacter == 'dspt'">
                            <span>时效: {{ logistics.lwstTmeff }}-{{ logistics.hghtTmeff }}{{ logistics.tmeffUnit}}</span>
                            <span></span>
                            <span class="card-right">
                                <span>价格:</span>
                                <span class="card-right-size">{{ logistics.lwstPrice }}-{{ logistics.hghtPrice }}</span>
                                <span>/{{logistics.offerCurr}}</span>
                            </span>
                        </div>
                    </template>
                </AdtListItem>
            </div>
        </div>
    </div>
</template>

<script>
import paginator from '@/mixins/paginator'
import AdtLogisticsRoute from '@/components/adt-logistics-route/Index'
import AdtListItem from '@/components/adt-list-item/Index'
import validateRules from '@/libs/validateRules'
import {
    apiLogisticsSchemeDspt,
    apiLogisticsSchemeMkt,
    apiRecommendPass,
    apiRecommendCheck,
    apiRecommendRoute,
    apiRecommendAll,
    apiRecommendCheckCstm,
    apiRecommendRouteCstm,
    apiRecommendAllCstm
    } from '@/api/logistics'
export default {
    mixins: [paginator],
    components: {
        AdtLogisticsRoute,
        AdtListItem
    },
    props: {
        value: ''
    },
    data() {
        return {
            search: {},
            isModalRecommend: false,
            distinguishCharacter: '',
            formInline: {
                remark: ''
            },
            ruleInline: {
                remark: [validateRules.required('原因')[0]],
            },
            isRecommend: 0,  // 默认为0，拒绝为1，销售同意/调度默认为2，客户同意为3
            // mktDiff: 0, // 默认0， 销售 拒绝为1，同意为2
            isCheckbox: false,
            // tabsSwitch: 0, // 默认为0，调度为1，销售为2
            titleNames: [],
            confirmStatus: '0', // 判断当前是已确认还是未确认  1为已确认，2为未确认
            recommendNames: ['已推荐客户方案', '未推荐客户方案'],
            confirmNames: ['已确认方案', '未确认方案'],
            btnIndex: 0,
            selectList: [], // 勾选的数据
            selectListData: [], // 勾选去重后的数据
            auditStatusList: [
                {code: 'unreviewed', status: '未审核'},
                {code: 'stock', status: '未货好'},
                {code: 'progress', status: '执行中'}
            ],
            enableStatusList: [
                {code: 'unreviewed', status: '已启用'},
                {code: 'stock', status: '未启用'},
                {code: 'progress', status: '已启用'}
            ],
            listData: [],
            listDatas: [
                {
                    statusCode: 'disagree',
                    status: '需求未同意',
                    logisticsPlanNumber: '物流方案编号1',
                    time: '时效',
                    price: '价格',
                    matchScheme: '1',
                    roadList: [
                        {
                            departurePort: '重庆1',
                            destinationPort: '香港',
                            transport: '海运',
                            type: '国内集装箱'
                        },
                        {
                            departurePort: '香港',
                            destinationPort: '纽约',
                            transport: '空运',
                            type: '国内集装箱'
                        },
                        {
                            departurePort: '纽约',
                            destinationPort: '北京',
                            transport: '空运',
                            type: '国内集装箱'
                        }
                    ]
                },
                {
                    statusCode: 'disagree',
                    status: '需求未同意',
                    logisticsPlanNumber: '物流方案编号1',
                    time: '时效',
                    price: '价格',
                    matchScheme: '0',
                    roadList: [
                        {
                            departurePort: '重庆1',
                            destinationPort: '香港',
                            transport: '海运',
                            type: '国内集装箱'
                        },
                        {
                            departurePort: '香港',
                            destinationPort: '纽约',
                            transport: '空运',
                            type: '国内集装箱'
                        },
                        {
                            departurePort: '纽约',
                            destinationPort: '北京',
                            transport: '空运',
                            type: '国内集装箱'
                        }
                    ]
                },
                {
                    statusCode: 'disagree',
                    status: '需求未同意',
                    logisticsPlanNumber: '物流方案编号1',
                    time: '时效',
                    price: '价格',
                    matchScheme: '1',
                    roadList: [
                        {
                            departurePort: '重庆1',
                            destinationPort: '香港',
                            transport: '海运',
                            type: '国内集装箱'
                        },
                        {
                            departurePort: '香港',
                            destinationPort: '纽约',
                            transport: '空运',
                            type: '国内集装箱'
                        },
                        {
                            departurePort: '纽约',
                            destinationPort: '北京',
                            transport: '空运',
                            type: '国内集装箱'
                        }
                    ]
                }
            ]
        }
    },
    mounted() {
        this.getPassData()
    },
    methods: {
        getPassData () {
            // const routeData = JSON.parse(this.value)
            // this.listDatas = routeData.freightRouteList
            // console.log(routeData)
            // console.log(this.listDatas)
        },
        // 同步推荐模态框状态
        isShow (val) {
            this.isModalRecommend = val
        },
        dataInit () {
            this.getUserData()
            this.getData()
        },
        getUserData () {
            // 取本地的账号角色信息 调度：dspt，销售：mkt，客户：cstm，
            // this.JSON.parse(window.sessionStorage.getItem('goodsData'))
            this.distinguishCharacter = 'dspt'
            // this.distinguishCharacter = 'mkt'
            // this.distinguishCharacter = 'cstm'
            // 销售的tabs切换name
            if (this.distinguishCharacter == 'mkt') {
                this.titleNames = this.recommendNames
            }
            // 调度的tabs切换name
            if (this.distinguishCharacter == 'dspt') {
                this.titleNames = this.confirmNames
                this.isCheckbox = true
                this.isRecommend = 2
                this.btnIndex = 1
            }
        },
        getData (val) {
            const data = {
                    // "cstmStepId": "",
                    "freightReqCode": "LC202007070707070001",
                    "page": 0,
                    "routeCode": "RT1001",
                    "routeList": [
                    {
                        "departurePort": "20201001",
                        "destinationPort": "20201002",
                        "roadCode": "R001"
                    },
                    {
                        "departurePort": "20201002",
                        "destinationPort": "20201010",
                        "roadCode": "R002"
                    }
                    ],
                    "size": 2,
                    // 环节号，区分方案是已确认还是未确认 为空的话未进行操作;
                    // 调度 20001已确认， 20002未确认;
                    // 销售 mktStepId 20005已推荐客户方案，20006未推荐客户方案
                    // 客户 cstmStepId 已同意销售推荐 20009，已拒绝销售推荐20010
                    // "stepId": "20001"
                    }
            // 调度
            if (this.distinguishCharacter == 'dspt') {
                if (this.btnIndex == 1) {
                    data.stepId = '20001'
                } if (this.btnIndex == 2) {
                    data.stepId = '20002'
                }
                apiLogisticsSchemeDspt({data: data}).then( res => {
                    if (res.code == 200) {
                        this.listData = res.data.content
                    }
                })
            }
            // 销售/客户
            if (this.distinguishCharacter == 'mkt' || this.distinguishCharacter == 'cstm') {
                // 销售
                if (this.distinguishCharacter == 'mkt') {
                    // 销售还未操作第一次访问，初始化页面
                    if (this.confirmStatus == 0) {
                        data.mktFirst = 'true'
                    }
                    if (this.confirmStatus == 1) {
                        data.stepId = '20005'
                        data.mktFirst = 'false'
                    }
                    if (this.confirmStatus == 2) {
                        data.stepId = '20006'
                        data.mktFirst = 'false'
                    }
                }
                // 客户
                if (this.distinguishCharacter == 'cstm') {
                    data.stepId = '20005'
                    data.mktFirst = 'false'
                }
                apiLogisticsSchemeMkt({data: data}).then( res => {
                    if (res.code == 200) {
                        this.listData = res.data.content || []
                        // 销售
                        if (this.distinguishCharacter == 'mkt') {
                            // 控制销售同意隐藏按钮
                            if (this.listData[0].mktStepId == '20007') {
                                this.isRecommend = 2
                            }
                            // 控制销售拒绝隐藏按钮，页面置灰
                            if (this.listData[0].mktStepId == '20008') {
                                this.isRecommend = 1
                            }
                        }
                        // 客户
                        if (this.distinguishCharacter == 'cstm') {
                             // 控制销售同意隐藏按钮
                            if (this.listData[0].cstmStepId == '20009') {
                                this.isRecommend = 3
                            }
                            // 控制销售拒绝隐藏按钮，页面置灰
                            if (this.listData[0].cstmStepId == '20010') {
                                this.isRecommend = 1
                            }
                        }
                    }
                })
            }
        },
        // 拒绝推荐
        refuseRecommend () {
            this.isModalRecommend = true
        },
        // 同意推荐
        agreeRecommend () {
            const data = {
                freightReqCode:"LC202007070707070001",
                flag: true,
                roleType: this.distinguishCharacter
            }
            apiRecommendPass({ data: data }).then( res => {
                if (res.code == 200) {
                    if (this.distinguishCharacter == 'mkt') {
                        this.isRecommend = 2
                    }
                    if (this.distinguishCharacter == 'cstm') {
                        this.isRecommend = 3
                    }
                }
            })
        },
        // 拒绝方案推荐保存
        save () {
            const data = {
                freightReqCode:"LC202007070707070001",
                flag: false,
                roleType: this.distinguishCharacter,
                remark: this.formInline.remark
            }
            this.$refs["formInline"].validate((valid) => {
                if (valid) {
                    apiRecommendPass({ data: data }).then( res => {
                        if (res.code == 200) {
                            this.isRecommend = 1
                            this.isModalRecommend = false
                            // 重置新增模态框
                            this.$refs.formInline.resetFields()
                        }
                    })
                }
            })
        },
        // 取消货运需求
        cancel () {
            this.isModalRecommend = true
            // 重置新增模态框
            this.$refs.formInline.resetFields()
        },
        // 编辑货运要求
        handleFeatures () {
            // 客户跳转至简易货运要求页面
            if (this.distinguishCharacter == 'cstm') {
                this.$router.push({ path: '/freight-request/simple-entry'})
            }
            // 销售/调度跳转至详细货运要求页面
            if (this.distinguishCharacter == 'mkt' || this.distinguishCharacter == 'dspt') {
                this.$router.push({ path: '/freight-request/detail-entry'})
            }
        },
        // 路线切换
        clickPath (val) {
            console.log(val)
            this.getData(val)
        },
        handleBtn (index) {
            this.btnIndex = index + 1
            // 判断当前是已确认还是未确认  1为已确认，2为未确认
            this.confirmStatus = this.btnIndex
            this.getData()
        },
        // 推荐勾选
        recommendCheck () {
            const data = this.selectListData
            // 调度推荐勾选
            if (this.distinguishCharacter == 'dspt') {
                apiRecommendCheck({ data: data }).then(res => {
                    if (res.code == 200) {
                        console.log(res.data)
                    }
                })
            }
            // 销售推荐勾选
            if (this.distinguishCharacter == 'mkt') {
                apiRecommendCheckCstm({ data: data }).then(res => {
                    if (res.code == 200) {
                        console.log(res.data)
                    }
                })
            }
        },
        // 推荐全部
        recommendAll () {
            // 调度推荐全部
            if (this.distinguishCharacter == 'dspt') {
                apiRecommendAll({ data: data }).then(res => {
                    if (res.code == 200) {
                        console.log(res.data)
                    }
                })
            }
            // 销售推荐全部
            if (this.distinguishCharacter == 'dspt') {
                apiRecommendAllCstm({ data: data }).then(res => {
                    if (res.code == 200) {
                        console.log(res.data)
                    }
                })
            }
        },
        // 推荐当前
        recommendCurrent () {
            // 调度推荐当前
            if (this.distinguishCharacter == 'dspt') {
                apiRecommendRoute({ data: data }).then(res => {
                    if (res.code == 200) {
                        console.log(res.data)
                    }
                })
            }
            // 销售推荐当前
            if (this.distinguishCharacter == 'dspt') {
                apiRecommendRouteCstm({ data: data }).then(res => {
                    if (res.code == 200) {
                        console.log(res.data)
                    }
                })
            }
        },
        checkData (data, status) {
            data.check = status
            if (status == true) {
                this.selectList.push(data)
            }
            // 数组去重
            let arr = new Set(this.selectList)
            let arr2 = Array.from(arr)
            let arr3 = []
            // 将未勾选的数组去除掉
            arr2.forEach( element => {
                if (element.check == true) {
                    arr3.push(element)
                }
            })
            this.selectListData = arr3
            // 数据组装
            const arr4 = []
            arr3.forEach(item => {
                const obj = {
                    logisticsSchemeCode: item.logisticsSchemeCode,
                    freightReqCode: item.freightReqCode,
                    routeCode: item.routeCode
                }
                arr4.push(obj)
            })
            this.selectListData = arr4
            console.log(this.selectListData)
        },
        // 发起委托
        delegate (val) {
            const data = val
            this.$router.push({ path: '/logisstics-commission/add-single', query:{data:JSON.stringify(data)} })
        },
        // 收藏
        collect (val) {
            console.log("收藏")
        },
        // 取消收藏
        uncollect (val) {
            console.log("收藏")
        },
        // 推荐客户/销售
        recommend (val) {
            const data = [{
                logisticsSchemeCode: val.logisticsSchemeCode,
                freightReqCode: val.freightReqCode,
                routeCode: val.routeCode
            }]
            // 调度推荐销售
            if ( this.distinguishCharacter == 'dspt') {
                apiRecommendCheck({data: data}).then(res => {
                    if (res.code == 200) {
                        console.log(res.data)
                    }
                })
            }
            // 销售推荐给客户
            if (this.distinguishCharacter == 'mkt') {
                apiRecommendCheckCstm({ data: data }).then(res => {
                    if (res.code == 200) {
                        console.log(res.data)
                    }
                })
            }
        },
        edit (val) {
            // const data = val
            this.$router.push({ path: '/logistics-solutions/confirmation-logistics-plan'})
        },
        detail (val) {
            const data = val
            window.sessionStorage.setItem('logisticsData', JSON.stringify(data))
            this.$router.push({ path: '/logistics-solutions/scheme-details'})
        },
        // 调度确认方案
        confirm (val) {
            const data = val
            window.sessionStorage.setItem('logisticsData', JSON.stringify(data))
            this.$router.push({ path: '/logistics-solutions/determine-logistics-solution-service-provider'})
        },
        remove (val) {}
    },
}
</script>

<style lang="scss" scoped>
.content-none {
    pointer-events: none;
    opacity: 0.5;
    // cursor: not-allowed;
}
.btn-audit {
    float: right;
    margin-right: 20px;
    margin-top: -7px;
    .btn-recommend {
        width: 120px;
        margin-left: 20px;
        border: 1px solid
    }
}
.alart-box{
    margin-top: 10px;
}
.search-condition {
    display: flex;
    span{
    min-width: 70px;
    line-height: 30px;
}
}
.line-margin {
    margin-left: 30px;
}
.icon-action {
    margin: 0 10px;
}
.btn-pagina {
    display: flex;
    padding: 20px 0 0 0;
    .title {
        font-size: $font-size-large;
        font-family: Microsoft YaHei;
        font-weight: 400;
        width: 50%;
    }
